import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import { App } from './App';
import { Dashboard } from './Components/Dashboard';
import { TableAdminMaster } from './Components/TableAdminMaster';
import { FormAdminMaster } from './Components/FormAdminMaster';
import { BlankPageAdminMaster } from './Components/BlankPageAdminMaster';
import { ProfilePageAdminMaster } from './Components/ProfilePageAdminMaster';
import { NotFoundPage } from './Components/404Page';

import { TableForms } from './Components/TableForms';
import { CollapsibleComponent } from './Components/CollapsibleComponent';
import { CollapsibleType2 } from './Components/CollapsibleType2';


import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faEnvelope, faTrashAlt, faUserCircle, faTachometerAlt, faFileSignature, faFileInvoice, faTable, faCogs, faCircleNotch, faAngleRight, faAngleDown, faTimes, faThLarge } from '@fortawesome/free-solid-svg-icons'

library.add(faBars, faEnvelope, faTrashAlt, faUserCircle, faTachometerAlt, faFileSignature, faFileInvoice, faTable, faCogs, faCircleNotch, faAngleRight, faAngleDown, faTimes, faThLarge)

export class Router extends React.Component{
	render(){
		return(
			<BrowserRouter>
				<Switch>
					<Route path='/' component={Dashboard} exact />
					<Route path='/tableadminmaster' component={TableAdminMaster} />
					<Route path='/formadminmaster' component={FormAdminMaster} />
					<Route path='/blankpageadminmaster' component={BlankPageAdminMaster} />
					<Route path='/profilepageadminmaster' component={ProfilePageAdminMaster} />
					<Route path='/notfoundpage' component={NotFoundPage} />

					<Route path='/tableforms' component={TableForms} />
					<Route path='/collapsiblecomponent' component={CollapsibleComponent} />
					<Route path='/collapsibletype2' component={CollapsibleType2} />
				</Switch>
			</BrowserRouter>
		)
	}
}