import React from 'react';
import { Sidebar } from './../Template/Sidebar';
import { AdminNavbar } from './../Template/AdminNavbar';
import { Link } from 'react-router-dom'
import { CollapsibleBox12 } from './../Template/CollapsibleBox12';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Tooltip, Container, Row, Col, Card, CardBody, CardTitle, CardText, CardHeader,  Breadcrumb, BreadcrumbItem, Table, Form, FormGroup, Label, Input, Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

let fakeServer = {
	sampleData : {
		user: 'sandy',
		goods : [
			{
				id: 1,
				name: 'Pen',
				description: 'Black',
				total: 20,
				price: 100
			},
			{
				id: 2,
				name: 'Pencil',
				description: 'Blue',
				total: 10,
				price: 70
			},
			{
				id: 3,
				name: 'Eraser',
				description: 'White',
				total: 16,
				price: 100
			},
			{
				id: 4,
				name: 'Bag',
				description: 'Convers',
				total: 90,
				price: 190
			},
			{
				id: 5,
				name: 'Shoes',
				description: 'Old School',
				total: 100,
				price: 300
			}
		]
	}
}

export class TableForms extends React.Component{
	constructor(props){
		super(props);
		this.toggleModal = this.toggleModal.bind(this);
		this.state = {
			fakeData : {},
			defaultData: ['Pagani', 'Maserati', 'Bugati', 'Lamborgini', 'Maybach'],
			message: '',
			modal: false
		}
	}
	
	


	componentDidMount(){
		this.setState({
			fakeData: fakeServer
		})
	}

	addData(e){
		e.preventDefault();
		const { defaultData } = this.state;
		const nameCar = this.nameCar.value;
		const checkData = this.state.defaultData.includes(nameCar)
		console.log(nameCar)
		if(checkData){
			this.setState({
				message: 'Data Already Exist'
			})
		}else{
			nameCar != '' && this.setState({
				defaultData: [...this.state.defaultData, nameCar],
				message: ''
			})
		}

		
	}

	removeData(data){
		const afterRemoveData = this.state.defaultData.filter(defaultData => {
			return defaultData !== data;
		})

		this.setState({
			defaultData: [...afterRemoveData],
		})
	}

	removeAll(e){
		const alert = window.confirm('Delete All Data?')
		console.log(alert)
		if(alert == true){
			this.setState({
				defaultData: [],
				message: "No Data"
			})
		}else{
			this.setState({
				message: ''
			})
		}
		
	}

	toggleModal(data){
		console.log(data);
		this.setState({
			modal: !this.state.modal
		})
	}
	render(){
		let number = 1;
		let num = 1;
		console.log(this.state.defaultData.index);
		return(
			<section className='AdminMaster'>
				<Sidebar />
				<AdminNavbar />
				<div className='AdminMaster-Content'>
					<div className="TableForms-Admin-Master">
						<Container fluid>
							<Breadcrumb>
						        <BreadcrumbItem active className='BreadcrumbDashboard'>
						        	<Link to='/'> Dashboard </Link>
						        </BreadcrumbItem>
						        <BreadcrumbItem active className='BreadcrumbDashboard'>
						        	 Table Forms
						        </BreadcrumbItem>
						    </Breadcrumb>
							<Row>
								<CollapsibleBox12 title="Basic Form Input Table">
									<Table responsive hover bordered size="sm">
								        <thead>
								          <tr>
								            <th style={{width: "40px"}}>No</th>
								            <th>Data</th>
								            <th>Action</th>
								          </tr>
								        </thead>
								        <tbody>
								        	{this.state.defaultData.map((data, index) => {
								        		return(
								        			 <tr key={index}>
											            <td className='text-center'>{number++}</td>
											            <td>{data}</td>
											            <td>
											            	<FontAwesomeIcon icon='trash-alt' onClick={(e) => (this.removeData(data))} />
											            	<FontAwesomeIcon icon='file-invoice' onClick={(e) => (this.toggleModal(data))} />
											            </td>
											         </tr>
								        		)
								        	})}
								        </tbody>
								    </Table>

								    <Modal isOpen={this.state.modal} toggle={this.toggleModal} className={this.props.className}>
							          <ModalHeader toggle={this.toggleModal}>Edit Data</ModalHeader>
							          <Form>
								          <ModalBody>
											<FormGroup row>
												<Label for="exampleEmail" sm={3}>Data</Label>
													<Col sm={9}>
													    <input type="teks" className='form-control'/>
													</Col>
											</FormGroup>
								          </ModalBody>
								          <ModalFooter>
									            <Button color="primary" onClick={this.toggleModal}>Update</Button>{' '}
									            <Button color="" onClick={this.toggleModal}>Cancel</Button>
								          </ModalFooter>
							          </Form>
							        </Modal>

								    {this.state.message &&
								    	<Col lg='6' className='mx-auto text-center'>
								    		<CardTitle style={{color: 'red'}}> {this.state.message} </CardTitle>
								    	</Col>
								    }
								    <div className="FormInput">
								    	<Container>
								    		<Row>
								    			<Col lg='9' className='mx-auto'>
												    <Form onSubmit={(e) => (this.addData(e))}>
												        <FormGroup row>
												          <Label for="exampleEmail" sm={3}>Input Data</Label>
												          <Col sm={9}>
												            <input type="teks" className='form-control' placeholder="Type Your Data" ref={(input) => (this.nameCar) = input} />
												          </Col>
												        </FormGroup>
												       	<FormGroup className='text-center'>   		
												            <Button outline color="primary"> Add Data </Button>{' '}
												            <Button outline color="danger" onClick={(e) => (this.removeAll(e))}> Remove All Data</Button>
												      	</FormGroup>
												    </Form>
											   	</Col>
											</Row>
										</Container>
								    </div>	
								</CollapsibleBox12>
								<CollapsibleBox12 title="Onclick Form Input Table">
									<Table responsive hover bordered size="sm">
								        <thead>
								          <tr>
								            <th style={{width: "40px"}}>No</th>
								            <th> Data </th>
								            <th> Description </th>
								            <th> Total </th>
								            <th> Price </th>
								          </tr>
								        </thead>
								        <tbody>
								        	{this.state.fakeData.sampleData && this.state.fakeData.sampleData.goods.map((listData)=>{
								        		return(
								        			<tr key={listData.id}>
								        				<td> {num++} </td>
								        				<td> {listData.name} </td>
								        				<td> {listData.description} </td>
								        				<td> {listData.total} </td>
								        				<td> ${listData.price}  </td>
								        			</tr>
								        		)
								        	})}
								        </tbody>
								    </Table>
								    <div className="FormInput">
								    	<Container>
								    		<Row>
								    			<Col lg='9' className='mx-auto'>
												    <Form>
												        <FormGroup row>
												          <Label for="exampleEmail" sm={3}>Name</Label>
												          <Col sm={9}>
												            <input type="teks" className='form-control' placeholder="Name" />
												          </Col>
												        </FormGroup>
												        <FormGroup row>
												          <Label for="exampleEmail" sm={3}>Description</Label>
												          <Col sm={9}>
												            <input type="teks" className='form-control' placeholder="Description" />
												          </Col>
												        </FormGroup>
												        <FormGroup row>
												          <Label for="exampleEmail" sm={3}>Total</Label>
												          <Col sm={9}>
												            <input type="teks" className='form-control' placeholder="Total" />
												          </Col>
												        </FormGroup>
												        <FormGroup row>
												          <Label for="exampleEmail" sm={3}>Price</Label>
												          <Col sm={9}>
												            <input type="teks" className='form-control' placeholder="Price" />
												          </Col>
												        </FormGroup>
												       	<FormGroup className='text-center'>   		
												            <Button outline color="primary"> Add Data </Button>{' '}
												            <Button outline color="info"> Update Data </Button>{' '}
												            <Button outline color="warning"> Delete Data </Button>{' '}
												            <Button outline color="danger"> Delete All Data</Button>
												      	</FormGroup>
												    </Form>
											   	</Col>
											</Row>
										</Container>
								    </div>	
								</CollapsibleBox12>
							</Row>
						</Container>
					</div>
				</div>
				<div className='clearFix'> </div>
			</section>
		)
	}
}